import Vue from 'vue'
import axios from 'axios'
import router from '@/router'
import qs from 'qs'
import Cookies from 'js-cookie'
axios.defaults.timeout = 30000;//ÂìçÂ∫îÊó∂Èó¥
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';        //ÈÖçÁΩÆËØ∑Ê±ÇÂ§¥
// if (process.env.NODE_ENV === 'development') {
//   axios.defaults.baseURL = '/api'
// }
axios.create({
  // ËÆæÁΩÆË∂ÖÊó∂Êó∂Èó¥
  timeout: 60000
})
axios.defaults.headers.Authorization = localStorage['token'] || null
axios.interceptors.request.use(function (config) {
  // Do something before request is sent
  config.headers['x-csrf-token'] = Cookies.get('csrfToken')
   //Âú®ÂèëÈÄÅËØ∑Ê±Ç‰πãÂâçÂÅöÊüê‰ª∂‰∫ã
   if(config.method  === 'post'){
    config.data = qs.stringify(config.data);
}
  return config
}, function (error) {
  // Do something with request error
  return Promise.reject(error)
})
// Âú®ËøôÈáåÂØπËøîÂõûÁöÑÊï∞ÊçÆËøõË°åÂ§ÑÁêÜ
// Âú®ËøôÈáåÊ∑ªÂä†‰Ω†Ëá™Â∑±ÁöÑÈÄªËæë
axios.interceptors.response.use(res => {
    return res.data
}, error => {
  if (error.response.status === 401) {
      var timer=setTimeout(()=>{
        router.replace({
          path: '/login'
        })
        clear(timer)
      },3000);
    
  } else {
    return Promise.reject(error)
  }
})
//ËøîÂõû‰∏Ä‰∏™Promise(ÂèëÈÄÅpostËØ∑Ê±Ç)
export function fetchPost(url, params) {
  return new Promise((resolve, reject) => {
      axios.post(url, params)
          .then(response => {
              resolve(response);
          }, err => {
              reject(err);
          })
          .catch((error) => {
              reject(error)
          })
  })
}
//ËøîÂõû‰∏Ä‰∏™Promise(ÂèëÈÄÅgetËØ∑Ê±Ç)
export function fetchGet(url, param) {
  return new Promise((resolve, reject) => {
      axios.get(url, {params: param})
          .then(response => {
              resolve(response)
          }, err => {
              reject(err)
          })
          .catch((error) => {
              reject(error)
          })
  })
}
export default {
  fetchPost,
  fetchGet,
}


